import React, { Component } from 'react';
// @autobind 제거
import { observer, inject } from 'mobx-react';
import { TextField, Button, Box, Typography, Alert, CircularProgress, Grid } from '@mui/material';
import { authStore } from './store/AuthStore';
import { toastify, dialog } from '@/component';

@inject("sideBarStore")
@observer // observer만 남깁니다.
class LoginForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password: '',
      erEmail: false,
      erEmailMsg: '',
      erPw: false,
      failCount: 0
    };
  }

  // 화살표 함수는 자동으로 바인딩되므로 @autobind가 필요 없습니다.
  handleChange = (e) => {
    const name = e.target.name;
    this.setState({
      [name]: e.target.value,
      [name === 'email' ? 'erEmail' : 'erPw']: false
    });
  }

  validData = () => { // 화살표 함수로 변경
    const { email, password } = this.state;
    let erEmail = false, erEmailMsg = "", erPw = false;

    if (email === undefined || email.trim() === "") {
      erEmail = true;
      erEmailMsg = "이메일을 입력해주세요.";
    }
    else if (email.trim().match(/^([a-zA-Z0-9_\-\.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([a-zA-Z0-9\-]+\.)+))([a-zA-Z]{1,5}|[0-9]{1,3})(\]?)$/) == null) {
      erEmail = true;
      erEmailMsg = "유효하지 않은 이메일 형식입니다.";
    }

    if (password === undefined || password.trim() === "") {
      erPw = true;
    }

    this.setState({ erEmail: erEmail, erEmailMsg: erEmailMsg, erPw: erPw });
    return erEmail || erPw;
  }

  handleLogin = async () => { // async 추가
    const { email, password, failCount } = this.state;

    if (failCount >= 5) {
      dialog.error("5회 이상 실패하여 로그인이 제한되었습니다.\n페이지를 새로고침하거나 나중에 시도하세요.");
      return;
    }
    // 1. 유효성 검사 실행
    if (this.validData()) {
      return;
    }

    // 2. 로그인 시도 및 결과 확인
    const isSuccess = await authStore.login(email, password);

    // 3. 성공 시 후속 처리
    if (isSuccess) {
      this.setState({ failCount: 0 }); // 성공 시 초기화
      const { context } = this.props;

      const userInfo = { name: 'Admin', id: 'admin', isLogin: true };

      // 컨텍스트 값 설정
      if (context && typeof context.setUserInfo === 'function') {
        context.setUserInfo(userInfo);
      }

      let isAdmin = false;
      if (userInfo.id === "admin" && userInfo.isLogin === true) {
        isAdmin = true;
      }
      this.props.sideBarStore.loadMenus(isAdmin);

      toastify("관리자 로그인 성공");

      // 메인 페이지 이동
      if (this.props.navigate) {
        this.props.navigate("/");
      } else {
        console.warn("navigate 함수가 props에 존재하지 않습니다.");
      }
    } else {
      this.setState(prevState => ({ failCount: prevState.failCount + 1 }));
    }
  }

  render() {
    const { email, password, erEmail, erEmailMsg, erPw } = this.state;
    const { loading, errorMessage, successMessage } = authStore;

    return (
      <Grid container justifyContent="center" alignItems="flex-start" sx={{ height: '100vh', padding: 2 }}>
        <Grid item xs={12}>
          <Box
            sx={{
              display: 'flex',
              flexDirection: 'column',
              gap: 2,
              padding: 3,
              border: '1px solid #ccc',
              borderRadius: 2,
              boxShadow: 3,
              backgroundColor: 'var(--sg-gray-700)',
              width: 'min(90vw, 320px)',
              margin: '0 auto'
            }}
          >
            <Typography variant="h4" textAlign="center" gutterBottom>로그인</Typography>

            <TextField
              label="이메일"
              name="email"
              value={email}
              onChange={this.handleChange}
              error={erEmail}
              helperText={erEmailMsg}
              fullWidth
            />
            <TextField
              label="비밀번호"
              name="password"
              type="password"
              value={password}
              onChange={this.handleChange}
              error={erPw}
              helperText={erPw ? "비밀번호를 입력해주세요." : ""}
              fullWidth
            />

            <Button
              variant="contained"
              onClick={this.handleLogin}
              disabled={loading}
              fullWidth
              sx={{ height: 48 }}
            >
              {loading ? <CircularProgress size={24} color="inherit" /> : '로그인'}
            </Button>

            {/* <Box sx={{ display: 'flex', justifyContent: 'space-between', marginTop: 1 }}>
              <Button variant="text" color="secondary" onClick={() => this.props.onChangeTab(1)}>회원가입</Button>
              <Button variant="text" color="secondary" onClick={() => this.props.onChangeTab(2)}>비밀번호 찾기</Button>
            </Box> */}

            {errorMessage && <Alert severity="error">{errorMessage}</Alert>}
            {successMessage && <Alert severity="success">{successMessage}</Alert>}
          </Box>
        </Grid>
      </Grid>
    );
  }
}

export default LoginForm;